import React from "react";
import { Link } from "react-router-dom";

const AddBtn = props => {
  return (
    <Link to={props.linkTo} onClick={props.onClick} className="addBtn">
      <i className="fas fa-plus"></i>
      <ul>
        <li style={props.liStyle}>
          <Link to="/avia"></Link>
        </li>
        <li style={props.liStyle}>
          <Link to="/sea"></Link>
        </li>
        <li style={props.liStyle}>
          <Link to="/truck"></Link>
        </li>
      </ul>
    </Link>
  );
};

export default AddBtn;
